import React from 'react'
import {Card} from './index'

import {GithubContext} from '../context/Api'

const Section = () =>{
    const {job} = React.useContext(GithubContext)
    
    
    return (
        <div className="jobs">
        <section>
     {
         job.slice(0,20).map( jobs => (
            // const {id,type,url,created_at,company,company_url,location,title,
            //         description , how_to_apply,company_logo} = jobs;
            <Card 
            jobs = {jobs}
            key = {jobs.id}
          
            />
         
      )) }
      </section>
     
        </div>
    )
}
export default Section