import React from 'react'
import ReactMarkdown from 'react-markdown'


function Card ({jobs}){
    const [open, setOpen] = React.useState(false)
    function handleClick() {
      setOpen(true)
    
    }
    return (
    <div className="card-body">
    
    <div className="title">
    <span> <p>Job Title : </p>
     {jobs.title}</span>
   
    <span> <p>Company : </p> 
     {jobs.company}</span>
    </div>
    <div className="subtitle">
    <p>Date :</p>
    {new Date(jobs.created_at).toLocaleDateString()}
    </div>
    <div className="type">
    <p>Job Type :</p>
    {jobs.type}
    <br/>
    <p>Job Location</p>
    {jobs.location}
    </div>
    <div style={{ wordBreak: 'break-all' }}>
    <p>How to Apply :</p>
    <ReactMarkdown children={jobs.how_to_apply.replace(/<\/?([a-z][a-z0-9]*)\b[^>]*>/gi, '')} />
  </div>
  
  <img className="img-company" height="50" alt={jobs.company} src={jobs.company_logo} />
  <div className="apply">
  <p>Apply here : </p>
  {jobs.how_to_apply.replace(/<\/?([a-z][a-z0-9]*)\b[^>]*>/gi, '') }
  
  </div>
  { !open && <button className="btn" onClick= {
    ()=>handleClick()
  }> Click More</button> }
  {open && 
    <>
    <a href={jobs.company_url} >{jobs.company_url}</a>
  <div className="desc">{jobs.description.replace(/<\/?([a-z][a-z0-9]*)\b[^>]*>/gi, '')}</div>
  <button className="btn" onClick= {() => setOpen(false)}>See less</button>
  </>

  }
    </div>
    )
}

export default Card;