import React, { useState } from 'react'
import {Navbar,Section ,Footer} from '../components'



const Dashboard = () => {
   const [dark , setDark] = useState(false)
    
    return (
        <div className={`main ${dark?'dark':''}`}>
      
     
       <Navbar/>
       {dark && 
        <button className="btnd" onClick={() => setDark(false)}>Light</button>
        }
        {!dark && 
        <button className="btnl" onClick={() => setDark(true)}>Dark</button>
        }
    <Section/>
    <Footer/>
     </div>
    )
}

export default Dashboard