import React, { useState, useEffect } from 'react'


import axios from 'axios'

const rootUrl ='https://cors-anywhere.herokuapp.com/https://jobs.github.com/positions.json'

const GithubContext = React.createContext();



const GithubProvider = ({ children }) => {

    
    const [job, setJob] = useState([])
    const [isloading, setIsLoading] = useState(false)
    const [error, setError] = useState({ show: false, msg: '' })

    

    const GithubJob = async () => {
        // axios.get(rootUrl)
        // .then(function(response){
        //     console.log(response);
            
        // })
    

        const response = await axios(`${rootUrl}`)
            .catch(err => console.log(err))
    
        if (response) {
            const job = response.data
        
            setJob(job)
            // const {type,url,created_at,company,company_url,location,title,
            //     description , how_to_apply,company_logo} = response.data
    }
    }
   

    const searchGithubJob = async (job) => {
        toggleError()
        setIsLoading(true)
        const response = await axios(`${rootUrl}description=${job}`)
            .catch(err => console.log(err))

        if (response) {
            setJob(response.data)
        }

    }
    function toggleError(show = false, msg = '') {
        setError({ show, msg })
    }
    useEffect(() => {
        GithubJob()
    }, [])

  


    return <GithubContext.Provider value={{
        job, searchGithubJob, isloading, error ,GithubJob
    }}> {children} </GithubContext.Provider>

}



export { GithubProvider, GithubContext}
 


